




export default function Pricing() {
  const [billing, setBilling] = useState<"monthly" | "annual">("monthly");

  return (
    <section className="min-h-screen px-6 py-20 bg-gray-100 dark:bg-black">
      <h2 className="text-4xl md:text-5xl font-bold text-center bg-gradient-to-r from-pink-500 via-blue-500 to-purple-500 text-transparent bg-clip-text mb-12">
        Pricing
      </h2>

      {/* Toggle */}
      <div className="flex justify-center mb-16">
        <div className="inline-flex items-center rounded-full bg-white dark:bg-gray-800 p-1 shadow-xl">
          <button
            onClick={() => setBilling("monthly")}
            className={`px-5 py-2 rounded-full text-sm font-semibold transition-all duration-300 ${
              billing === "monthly"
                ? "bg-gradient-to-r from-pink-500 via-blue-500 to-purple-500 text-white shadow-md"
                : "text-gray-600 dark:text-gray-300"
            }`}
          >
            Monthly
          </button>
          <button
            onClick={() => setBilling("annual")}
            className={`px-5 py-2 rounded-full text-sm font-semibold transition-all duration-300 ${
              billing === "annual"
                ? "bg-gradient-to-r from-pink-500 via-blue-500 to-purple-500 text-white shadow-md"
                : "text-gray-600 dark:text-gray-300"
            }`}
          >
            Annual
          </button>
        </div>
      </div>

      {/* Pricing Cards */}
      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-10">
        {plans[billing].map((plan, idx) => (
          <motion.div
            key={idx}
            initial={{ opacity: 0, y: 40 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: idx * 0.15 }}
            className={`relative rounded-3xl shadow-2xl bg-white dark:bg-gray-900 px-10 py-16 flex flex-col justify-between min-h-[580px] border border-gray-300 dark:border-gray-800 transition-transform ${
              idx === 1
                ? "z-20 scale-110 shadow-[0_0_40px_rgba(0,0,0,0.15)]"
                : "z-10 scale-100"
            }`}
            style={{
              transform: "perspective(1000px) rotateX(0.5deg)",
            }}
          >
            {/* Badge */}
            {plan.badge && (
              <div className="absolute top-4 right-4 text-xs font-semibold px-3 py-1 rounded-full bg-gradient-to-r from-pink-500 via-blue-500 to-purple-500 text-white shadow-md animate-gradient-x">
                {plan.badge}
              </div>
            )}

            {/* Header */}
            <div className="text-center">
              <h3 className="text-2xl font-extrabold mb-2 bg-gradient-to-r from-pink-500 via-blue-500 to-purple-500 text-transparent bg-clip-text">
                {plan.title}
              </h3>
              <div className="text-4xl font-bold text-gray-800 dark:text-white">
                {plan.price}
              </div>
              {plan.save && (
                <p className="text-sm text-green-600 dark:text-green-400 font-semibold mt-2">
                  {plan.save}
                </p>
              )}
            </div>

            {/* Features */}
            <ul className="mt-8 space-y-4">
              {plan.features.map((feature, i) => (
                <li
                  key={i}
                  className="flex items-center gap-3 text-gray-700 dark:text-gray-300 text-base"
                >
                  <CheckCircle className="text-green-500" size={20} />
                  {feature}
                </li>
              ))}
            </ul>
          </motion.div>
        ))}
      </div>
    </section>
  );
}
